// -----------------------------------------------------------------------------
// This file contains all main js.
// -----------------------------------------------------------------------------

$(document).ready(function() {
    /********************************/
    /*responsive top cover*/
    /********************************/
    var hgt_window = $(window).height();
    var hg_header = $('#header-wtf').height();
    var count_hgt = hgt_window - hg_header;
    $('#bg-cover-top').css('height', count_hgt);
    $('.ct-cover-top').css('height', count_hgt);
    $('#content-fe-wtf').css('margin-top', hg_header);

    var ct_tops = $('#ct-tops').height();
    var count_ct_tops = ct_tops + 400;
    if (window.matchMedia('(max-height: '+count_ct_tops+'px)').matches) {
        $('#ct-tops').css({
            'position' : 'unset',
            'transform' : 'unset',
            'margin' : 'auto 20px',
            'display' : 'block',
            'width' : 'calc(100% - 40px)',
            'padding-top' : '100px',
        });
        $('#bg-cover-top').css({
            'height' : ct_tops + 350
        });
    }

    /********************************/
    /*menu scrollbar*/
    /********************************/
    $("#arrw-wtf-left").click(function () {
        var leftPos = $('#wtf-tab').scrollLeft();
        $(".dv-scrbamn-hotItems").animate({
            scrollLeft: leftPos - 200
        }, 800);
    });

    $("#arrw-wtf-right").click(function () {
        var leftPos = $('#wtf-tab').scrollLeft();
        $(".dv-scrbamn-hotItems").animate({
            scrollLeft: leftPos + 200
        }, 800);
    });
    
    /********************************/
    /*show & hide arrow menu tab*/
    /********************************/
    var jumlahmenu = 0;
    $('#wtf-tab li').each(function(index) {
        jumlahmenu += parseInt($(this).width());
    });
    var widthTab = $('.scroll-menu-tab').width();
    
    if(jumlahmenu > widthTab){
        console.log('masuk');
        $('.scroll-menu-tab')
            .mouseover(function(){
                $('.arrow-scrl').css('visibility', 'visible');
            })
            .mouseout(function(){
                $('.arrow-scrl').css('visibility', 'hidden');
            });
    }
});